.contacts {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
}

.contacts .wrapper {
	display: flex;
	flex-direction: column;
}

.contacts__container {
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contacnt__phone {
	font-size: 32px;
	color: $elem-color;
	text-decoration: underline;
}

.contacts__social {
	margin-top: 20px;
	margin-bottom: 20px;
	display: flex;
	justify-content: space-around;
}

.contacts__social-icon {
	width: 50px;
	height: 50px;

	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	opacity: 0.6;

	&:hover {
		opacity: 1;
	}
	&:active {
		opacity: 0.4;
	}
	&:first-child {
		margin-right: 15px;
	}
}

.contacts__social-icon--vk {
	background-image: url('../img/vk-icon.svg');
}

.contacts__social-icon--instagram {
	background-image: url('../img/instagram-icon.svg');
}

.contact__map {
	margin-top: 20px;
	display: flex;
	flex-direction: column;
	width: 100%;

	& iframe {
		border: none;
		border-radius: 5px;
		width: 100%;
		height: 350px;
	}
}

.social-container {
	display: flex;
	justify-content: space-between;
	width: 240px;
}

.contacts__adress {
	color: $text-color;
	font-size: 18px;
	opacity: 0.6;
}

@media (min-width: 960px) {
	.contacts {
		padding: 0;
		width: 40%;
	}

	.contacts__social {
		margin-top: 20px;
		margin-bottom: 0;
		display: flex;
		justify-content: space-between;
		width: auto;
	}	

	.contact__map {
		margin-top: 40px;
	}

	.contacts__adress {
		margin-top: 20px;
	}
}