@keyframes rotate {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(90deg);
	}
}

@keyframes slideleft {
	0% {
		transform: translateX(100%);
	}

	100% {
		transform: translateX(0);
	}
}

.navigation {
	position: relative;
	display: flex;
	flex-direction: column;
}

.navigation__toggle {
	position: absolute;
	top: -60px;
	right: 0;
	width: 35px;
	height: 40px;

	background-color: transparent;
	border: none;
	cursor: pointer;
	outline: none;
}

.navigation--closed .navigation__toggle::before {
	position: absolute;
	top: 5px;
	right: 0;
	content: "";
	height: 4px;
	width: 35px;

	background-color: #ffffff;
	box-shadow: 0 12px 0 0 #ffffff, 0 24px 0 0 #ffffff;
	transition: 0.4s ease-out;
}

.navigation--opened .navigation__toggle {
	animation: rotate 0.4s ease-out;
	&::after,
	&::before {
	    content: "";
	    position: absolute;
	    top: 18px;
	    right: 0px;
	    width: 35px;
	    height: 4px;

	    background-color: #ffffff;
	}

	&::before {
	    transform: rotate(45deg);
	}

	&::after {
	    transform: rotate(-45deg);
	}

}

.navigation--closed .navigation__list {
	animation: slideleft 0.4s;
	display: none;
}

.navigation--opened .navigation__list {
	animation: slideleft 0.4s;
	display: block;
}

.navigation__item {
	width: 100%;
	&:not(:last-child) {
		border-bottom: 1px solid #333333;
	}
}

.navigation__link {
	display: block;
	padding: 20px;

	text-align: center;
	text-transform: uppercase;
	color: $text-color;
	font-size: $links-fs-mobile;

	&:hover {
		background-color: #333333;
	}	
}

@media (min-width: 960px) {
	.navigation .navigation__list {
		display: flex;
		flex-wrap: wrap;
	}

	.navigation__toggle {
		display: none;
	}

	.navigation__link {
		color: $text-color;
		font-size: 14px;
	}

	.navigation__list {
		display: flex;
	}

	.navigation__item {
		width: auto;
		&:not(:last-child) {
			border: none;
		}
	}
}