.features__title {
	font-size: $links-fs-mobile;
	color: $elem-color;
	z-index: 2;
	background-color: $bg-color;
	padding-right: 10px;
	padding-left: 10px;
}

.features__description {
	width: 100%;
	margin-top: 20px;
	font-size: $description-fs-mobile;
}

.features__item {
	margin-bottom: 40px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;

	&::after {
		position: absolute;
		content: "";
		top: 10px;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $elem-color;
		z-index: 1;
	}
}

@media (min-width: 660px) {
	.features {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}

	.features__item {
		box-sizing: border-box;
		width: 260px;

		&::after {
			top: 15px;
			height: 2px;
			width: 70%;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	.features__title {
		text-align: center;
		padding-right: 20px;
		padding-left: 20px;
		font-size: $titles-fs-mobile;
	}

	.features__description {
		font-size: $description-fs-tablet;
		text-align: center;
	}
}

@media (min-width: 960px) {
	.features {
		justify-content: space-between;
	}

	.features__item {
		width: 300px;
		padding-left: 15px;
		padding-right: 15px;

		&::after {
			top: 15px;
			height: 2px;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	.features__title {
		text-align: center;
		padding-right: 20px;
		padding-left: 20px;
		font-size: $titles-fs-desktop;
	}

	.features__description {
		font-size: $description-fs-desktop;
	}
}
