.page-header {
	padding-top: 20px;
	background-color: $bg-color;
}

.page-header__logo img {
	width: 230px;
}

@media (min-width: 960px) {
	.page-header .wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}

	.page-header__logo img {
		width: 300px;
		margin-right: 20px;
	}
}