.about {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
	color: $text-color;
	font-size: $links-fs-mobile;
}

.about__description {
	margin-top: 20px;
	margin-bottom: 40px;
	& span {
		font-size: 20px;
		color: $elem-color;
	}
}

.about__image img {
	width: 100%;
}

@media (min-width: 660px) {
	.about {
		font-size: $links-fs-tablet;
	}
	
	.about__container {
		display: flex;
	}

	.about__image {
		margin-right: 40px;
		width: 50%;
	}

	.about__description {
		margin-top: 0;
		width: 50%;

		& span {
			font-size: 22px;
		}
	}

	.about__features {
		margin-top: 40px;
	}
}

@media (min-width: 960px) {
	.about {
		font-size: $links-fs-desktop;
		padding-top: 60px;
		padding-bottom: 60px;
	}

	.about__description {
		line-height: 28px;
		& span {
			font-size: 26px;
		}
	}

	.about__image {
		margin-right: 60px;
	}

	.about__features {
		margin-top: 80px;
	}
}