@font-face {
	font-family: 'Roboto';
	src: url("../fonts/RobotoCondensed-Regular.woff") format("woff");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
    font-family: 'Roboto';
	src: url("../fonts/RobotoCondensed-Bold.woff") format("woff");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

* {
	margin: 0;
	padding: 0;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

body {
	min-width: 320px;
	font-family: 'Roboto', 'Arial', sans-serif;
	background-color: $bg-color;
}

.visually-hidden {
	position: absolute;
	clip: rect(1px 1px 1px 1px);
	padding: 0;
	border: 0;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.wrapper {
	width: 87%;
	margin: 0 auto;
}

.title {
	padding-top: 10px;
	padding-bottom: 10px;
	font-size: $titles-fs-mobile;
	text-transform: uppercase;
	font-weight: normal;	
	color: $text-color;
	border-bottom: 1px solid #333333;
}

.button {
	padding: 15px;
	width: 200px;
	background-color: transparent;
	border: 2px solid $elem-color;
	border-radius: 5px;
	color: $elem-color;
	font-size: $links-fs-mobile;
	text-transform: uppercase;
	cursor: pointer;
	outline: none;

	&:hover {
		background-color: $elem-color;
		color: #ffffff;
	}

	&:active {
		color: rgba(255, 255, 255, 0.4);
	}
}

@media (min-width: 660px) {
	.wrapper {
		width: 90%;
		max-width: 660px;
	}

	.title {
		font-size: $titles-fs-tablet;
	}
}

@media (min-width: 960px) {
	.wrapper {
		width: 96%;
		max-width: 960px;
	}

	.title {
		font-size: $titles-fs-desktop;
	}

	.container {
		padding-top: 80px;
		padding-bottom: 80px;
		display: flex;
		justify-content: space-around;
		width: 96%;
		max-width: 960px;
		margin: 0 auto;

		.wrapper {
			width: 100%;
		}
	}
}