.feedback {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
	color: $text-color;
}

.feedback__description {
	margin-top: 40px;
	text-align: center;
	font-size: $description-fs-mobile;
}

.feedback__contaner {
	position: relative;
}

@media (min-width: 960px) {
	.feedback {
		padding: 0;
		width: 40%;
	}

	.feedback__description {
		margin-top: 60px;
		text-align: center;
		font-size: $titles-fs-tablet;
		padding-right: 30px;
		padding-left: 30px;
	}

	.feedback .wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
}

