.sale {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
	color: $text-color;
}

.sale .wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.sale__description {
	width: 100%;
	margin-top: 20px;
	text-align: center;
	font-size: $description-fs-mobile;
	line-height: 28px;

	& .sale__text {
		color: $elem-color;
		font-size: 24px;
	}
}

.sale__old-price {
	margin-left: 10px;
	position: relative;
	color: #666666;

	&::after {
		content: "";
		position: absolute;
		top: 8px;
		left: 0;
		height: 3px;
		width: 50px;
		transform: rotate(-15deg);

		background-color: #666666;
	}
}

.sale__new-price {
	margin-left: 10px;
	font-size: 28px;
	color: $elem-color;
}

.sale__button {
	margin-top: 40px;
}

@media (min-width: 960px) {
	.sale {
		padding-top: 80px;
		padding-bottom: 60px;
	}

	.sale__description {
		margin-top: 40px;
		font-size: $titles-fs-desktop;
		line-height: 50px;

		& .sale__text {
			color: $elem-color;
			font-size: 40px;
			line-height: 52px;
		}
	}

	.sale__new-price {
		font-size: 36px;
	}

	.sale__old-price {
		&::after {
			content: "";
			position: absolute;
			top: 15px;
			left: 0;
			height: 3px;
			width: 70px;
			transform: rotate(-15deg);

			background-color: #666666;
		}
	}
}