.slider {
	position: relative;
	margin-top: 40px;
	overflow: hidden;
}

.slider__controls {
	display: flex;
	justify-content: space-between;
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 3;
}

.slider__button {
	position: relative;
	width: 35px;
	height: 35px;

	background-color: rgba(0, 0, 0, 0.3);
	border: 2px solid $elem-color;
	border-radius: 50%;
	opacity: 0.6;
	cursor: pointer;
	outline: none;

	&::after {
		content: "";
		position: absolute;
		top: 8px;
		left: 8px;

		width: 10px;
		height: 10px;

		border: 5px solid $elem-color;
		border-bottom: none;
		border-right: none;
	}

	&:hover {
		opacity: 1;
	}

	&:active {
		opacity: 0.4;
	}
}

.slider__button--prev {
	&::after {
		transform: rotate(-45deg);
	}
}

.slider__button--next {
	&::after {
		transform: rotate(135deg);
	}
}

.slider__slides {
	overflow: hidden;
	height: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.slider__item img {
	width: 320px;
}

@media (min-width: 660px) {
	.slider__slides {
		height: 550px;
	}

	.slider__item img {
		width: 620px;
	}
}

@media (min-width: 960px) {
	.slider__slides {
		height: 650px;
	}

	.slider__item img {
		width: 650px;
	}

	.slider__button {
		position: relative;
		width: 60px;
		height: 60px;

		&::after {
			content: "";
			position: absolute;
			top: 15px;
			left: 16px;

			width: 16px;
			height: 16px;

			border: 6px solid $elem-color;
			border-bottom: none;
			border-right: none;
		}

		&:hover {
			opacity: 1;
		}

		&:active {
			opacity: 0.4;
		}
	}
}