.page-footer {
	border-top: 1px solid #333333;
	padding-top: 40px;
	background-color: $bg-color;
}

.page-footer__logo img {
	width: 150px;
}

.page-footer__menu {
	margin-top: 20px;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.page-footer__item {
	margin-bottom: 15px;
	margin-right: 15px;
}

.page-footer__link {
	font-size: 16px;
	color: $text-color;
	opacity: 0.3;

	&:hover {
		opacity: 0.8;
	}
}

.page-footer__container	{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.page-footer__copyright {
	width: 200px;
	margin-top: 40px;
	margin-bottom: 40px;
	text-align: center;
}

.page-footer__copyright p {
	color: $text-color;
	opacity: 0.3;
	font-size: 12px;
}

.page-footer__copyright a {
	font-size: 12px;
	color: $elem-color;
	

	&:hover {
		text-decoration: underline;
		color: gold;
	}
}

@media (min-width: 960px) {
	.page-footer {
		padding-top: 20px;
		padding-bottom: 60px;
	}

	.page-footer__logo img {
		width: 200px;
	}

	.page-footer .wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.page-footer__container	{
		flex-direction: row;
	}

	.page-footer__menu {
		margin-top: 0;
		margin-right: 30px;
	}

	.page-footer__item {
		margin-bottom: 0;
	}

	.page-footer__link {
		font-size: 20px;
	}

	.page-footer__copyright {
		margin-top: 15px;
		margin-bottom: 0;
		text-align: right;
	}

	.page-footer__copyright a {
		font-size: 14px;
	}
}